const makeCardClickable = articleClass => {
  if (!articleClass) {
    return;
  }

  document.querySelectorAll(`${articleClass}__post`).forEach(post => {
    const postLink = post.querySelector(`${articleClass}__post-image`);
    post.querySelectorAll("a").forEach(link => {
      link.addEventListener("click", event => {
        event.preventDefault();
      });
    });
    post.addEventListener("click", () => {
      window.open(postLink.href, "_blank");
    });
  });
};

export default makeCardClickable;
