import replaceQuote from "./replaceQuote.js";
import removeIDfromCtaButton from "./removeIDfromCtaButton.js";
import makeCardClickable from "../listing/makeCardClickable.js";
import moveToSomewhere from "../listing/moveToSomewhere.js";

(() => {
  replaceQuote();
  removeIDfromCtaButton();

  makeCardClickable(".blog-more-posts");

  moveToSomewhere({
    totalItemsClass: ".blog-more-posts__post",
    itemToMoveClass: ".blog-more-posts__post-date",
    placeToMoveClass: ".blog-more-posts__post-content > div"
  });

  moveToSomewhere({
    totalItemsClass: ".blog-more-posts__post",
    itemToMoveClass: ".blog-more-posts__post-preheader",
    placeToMoveClass: ".blog-more-posts__post-meta"
  });
})();
